/*
    --footer-background-color
    --footer-text-color
    --footer-link-color
    --footer-text-second-color
	--footer-link-second-color
	--footer-heading-color
	--footer-text-color
 */

@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin fixed($t, $l, $b, $r) {
    position: fixed;
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }    
}
@mixin border-inline-start($value) {
    border-inline-start:$value;
}  
@mixin border-inline-end($value) { 
    border-inline-end:$value;
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin flex-end {
    justify-content: flex-end;
}

footer {
	background: var(--footer-background-color);
}
.section-footer-widget {
	.block-title {
		margin-bottom: 15px;
		color: var(--footer-heading-color);
		p {
			line-height: 1.45em;
			margin: 0;
		}
	}
	.block-inner {
		padding-bottom: 30px;
	}
	.has-border {
		.block-inner {
			margin-bottom: 30px;
			border-bottom: 1px solid var(--footer-border-color);
		}
	}
	.block-address {
		border: 1px solid #E3E3E3;
		padding: 10px 20px;
		border-radius: var(--border-radius);
		margin-bottom: 17px;
		max-width: 295px;
		color: var(--footer-text-color);
		.icon {
			@include margin-end(13px);
			path {
				stroke: var(--footer-text-color);
			}
		}
		.address {
			line-height: 1.45em;
			font-weight: 500;
		}
	}
	.block-information {
		color: var(--footer-text-second-color);
		p {
			margin: 13px 0 0;
			line-height: 1.5em;
			a {
				text-decoration: underline;
				text-underline-offset: 1px;
				color: var(--primary-color);
			}
		}
	}
	.block-contact {
		font-weight: 500;
		line-height: 1.2em;
		color: var(--footer-text-color);
	}
	.block-social {
		margin-top: 25px;
		ul {
			margin: 0 -8.5px;
			li {
				padding: 0 8.5px;
			}
			a {
				display: flex;
			}
			svg {
				width: 20px;
				height: 20px;
				path {
					fill: var(--footer-text-second-color);
				}
			}
		}
	}
	.block-type-menu {
		.list-item {
			a {
				padding: 2px 0;
				display: block;
				line-height: 1.5em;
				color: var(--footer-link-color);
			}
		}
	}
	@media (min-width: 750px) {
		.flex {
			margin: 0 -10px;
			>div {
				padding: 0 10px;
			}
		}
		.has-border .block-inner {
			padding-bottom: 5px;
			border-bottom: 0;
			@include border-inline-end(1px solid var(--footer-border-color));
		}
		.block-type-address {
			.block-inner {
				@include margin-end(30px);
				@include padding-end(30px);
			}
		}
	}
}
.section-footer {
	.section-container {
		>.lg-flex {
			align-items: center;
		}
	}
	.footer-mobile-menu {
		button {
			border-top: 1px solid var(--footer-border-color);
			border-bottom: 1px solid var(--footer-border-color);
			padding: 30px 0;
			display: flex;
			align-items: center; 
			width: 100%;
			color: var(--footer-text-color);
			.text {
				@include padding-start(13px);
				font-size: 17px;
				line-height: 1.5em;
			}
			svg {
				path {
					stroke: var(--footer-text-color);
				}
			}
			.icon-on-close {
				display: none;
			}
			.icon-on-show {
				display: flex;
			}
			&.active {
				.icon-on-show {
					display: none;
				}
				.icon-on-close {
					display: flex;
				}
			}
		}
	}
	.footer-menu-list {
		margin: 0 -15px;
		>li {
			padding: 0 15px;
			a {
				color: var(--footer-link-second-color);
				text-transform: capitalize;
			}
		}
	}
	.copyright {
		line-height: 1.5em;
		margin: 9px 0;
		color: var(--footer-text-second-color);
	}
	.footer-payment-method {
		>ul {
			margin: 0 -5px;
		}
		li {
			padding: 0 5px;
			display: flex;
			svg {
				height: 28px;
				max-width: 32px;
			}
		}
	}
	@media (min-width: 1000px) {
		.footer-payment-method {
			display: flex;
			@include flex-end;
		}
	}
}
footer-mobile-navigation {
	@include fixed(100%,0,0,0);
	background: var(--footer-background-color);
	padding: 16px;
	z-index: -1;
	overflow: hidden;
    overflow-y: auto;
    opacity: 0;
    visibility: hidden;
    transition: all .2s ease-in-out;
    &.active {
    	z-index: 222;
    	opacity: 1;
    	visibility: visible;
    	top: 0;
    }
	.popup-close {
		@include ab(16px,n,n,16px);
		display: flex;
		padding: 13px;
		background: var(--footer-border-color);
		border-radius: var(--border-radius);
	}
	.block-title {
		color: var(--footer-heading-color);
		margin-bottom: 15px;
		p {
			line-height: 1.45em;
			margin: 0;
		}
	}
	.block-inner {
		padding-bottom: 22.5px;
		margin-bottom: 22.5px;
		border-bottom: 1px solid var(--footer-border-color);
	}
	.block-item:last-child {
		.block-inner {
			border-bottom: 0;
			margin-bottom: 0;
			padding-bottom: 0;
		}
	}
	.list-item {
		a {
			text-transform: capitalize;
			padding: 5px 0;
			display: block;
			line-height: 1.5em;
			color: var(--footer-link-color);
		}
		>details {
			>summary {
				align-items: center;
				svg {
					path {
						fill: var(--footer-link-color);
					}
				}
			}
		}
	}
}
.section-static-section-icon-list {
	&.show-border {
		.section-content-inner {
			border-top: 1px solid var(--footer-border-color);
			border-bottom: 1px solid var(--footer-border-color);
		}
	}
	.group-text {
		color: var(--footer-text-second-color);
	}
}